.footer-logo {
  height: 50px;
}

.social_links {
  font-size: 20px;
  margin-right: 20px;
}

.app-footer {
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: #fafafa !important;
  border-top: 0.5px solid #80808057;
}

.header {
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
}

.navlink {
  margin: 0 20px;
  color: black;
  font-size: 14px;
  line-height: 20px;
  text-decoration: none;
}

.navlink:hover {
  color: #ea4446;
  text-decoration: none;
}

.active {
  color: #ea4446;
}

.login-a {
  text-decoration: none;
  max-width: 190px;
  width: 100%;
}

.login-button {
  display: inline-block;
  max-width: 300px;
  margin-top: 50px;
  border: 0;
  padding: 0 18px;
  text-align: left;
  width: 100%;
  height: 37px;
  border-radius: 4px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -moz-font-feature-settings: "liga"on;
  color: rgba(0, 0, 0, 0.84) !important;
  fill: rgba(0, 0, 0, 0.84) !important;
  box-shadow: 0 1px 7px rgba(0, 0, 0, 0.05);
  font: inherit;
  outline: none;
}

.button .svgIcon {
  vertical-align: middle;
  fill: rgba(0, 0, 0, 0.54);
  padding-right: 4px;
  height: 37px;
  display: inline-block;
}

@media (min-width: 320px) and (max-width: 767px) {
  .collapsable-navItems {
    margin-top: 5px;
    margin-bottom: 5px;
  }
}
